import { Button, FormControl, MenuItem, Select, SelectChangeEvent, Slider, TextField, Typography } from '@mui/material';
import React, { MutableRefObject, useRef, useState } from 'react';
import {v4 as uuid} from 'uuid';
import { LocalizationProvider, MobileDatePicker} from '@mui/x-date-pickers'
import { AdapterDateFns } from '@mui/x-date-pickers/AdapterDateFns'
import { fi } from 'date-fns/locale';
import { NavigateFunction, useNavigate } from 'react-router-dom';

interface Props {
    tehtavat : Tehtava[],
    tilastot : Tilasto[],
    setTehtavat : (arg0 : Tehtava[]) => void,
    setTilastot : (arg0 : Tilasto[]) => void,
    setValue : (arg0 : string) => void,
}

const Kirjaus : React.FC<Props> = ( {tehtavat, setTehtavat, tilastot, setTilastot, setValue} ) : React.ReactElement => {

    const kirjaus : MutableRefObject<any> = useRef<HTMLInputElement>(null);
    const [lukumaara, setLukumaara] = useState<string>("1");
    const [aika, setAika] = useState<number>(1);
    const [pvm, setPvm] = useState<Date>(new Date());

    const [virhe, setVirhe] = useState<string>("");
 
    const navigate : NavigateFunction = useNavigate();

    //Tallentaa tehtvn muistilistalle sek tilastoihin
    const tallenna = () : void => {

        let uusi : Tehtava = {
                                id : uuid(), 
                                kuvaus : kirjaus.current.value.toLowerCase(), 
                                suoritettu : pvm.getTime(),
                                teeAjassa : aika*Number(lukumaara)*24*60*60*1000,
        }
        let uusiTilasto : Tilasto = {
            kuvaus : uusi.kuvaus, 
            suoritettu : uusi.suoritettu,
        }
        /* Tallennus tapahtuu, mikli on annettu vhintn kaksi merkki ja tsmlleen samalla 
        nimell ei ole aiempaa merkint */
        if (uusi.kuvaus.length < 2) {
            setVirhe("Nimess pit olla vhintn kaksi merkki")
        } else if ( !tehtavat.find( (tehtava) => {return tehtava.kuvaus === uusi.kuvaus;} ) ){
            let uusiLista : Tehtava[] = [...tehtavat, uusi];
            setTehtavat(uusiLista);
            let uusiTilastoLista : Tilasto[] = [...tilastot, uusiTilasto];
            setTilastot(uusiTilastoLista);
            setValue("koti");
            navigate("/");
        } else {
            setVirhe("Tehtvn nimen pit olla yksilllinen")
            }
    }

    //Tallentaa sliderin arvon
    const handleSliderChange = (e : Event, value : number | number[] ) => {
        setAika(Number(value));
    };

    //Tallentaa alasvetovalikon arvon
    const handleChange = (e : SelectChangeEvent) : void => {
        setLukumaara((e.target.value as string));
    }

    return (
        <LocalizationProvider
            dateAdapter={AdapterDateFns}
            adapterLocale={fi}
        >

            <TextField 
                label="Tehtv"
                inputRef={kirjaus}
                fullWidth={true}
                style={{margin:"20px 0"}}
                error={Boolean(virhe)}
                helperText={virhe}
            />

            <Typography style={{marginBottom:"30px"}}>Aikavli, jolla tehtv tulisi suorittaa</Typography>

            <Slider
                min={1}
                max={12}
                defaultValue={1}
                valueLabelDisplay='on'
                onChange={handleSliderChange}
                style={{ width:"70%", paddingTop:"30px", margin:"0 5%"}}
            />

            <FormControl>
                <Select
                    value={lukumaara}
                    onChange={handleChange}
                >
                    <MenuItem value={1}>pv</MenuItem>
                    <MenuItem value={7}>vk</MenuItem>
                    <MenuItem value={31}>kk</MenuItem>
                </Select>
            </FormControl>

            <MobileDatePicker
                label="Suoritettu viimeksi"
                value={pvm}
                onChange={(uusiPvm : Date | null) => setPvm(uusiPvm!)}
                renderInput={(params : any) => <TextField fullWidth style={{marginTop:"20px"}}
                    {...params}
                />
                }
            />

            <Button 
                onClick={tallenna}
                variant='contained'
                fullWidth={true}
                style={{marginTop:"20px"}}
            >
                Tallenna
            </Button>

        </LocalizationProvider>
    )
}

export default Kirjaus;