import { BottomNavigation, BottomNavigationAction, Container, Typography } from '@mui/material';
import React, { useEffect, useRef, useState } from 'react';
import Kirjaus from './components/Kirjaus';
import Lista from './components/Lista';
import Tilasto from './components/Tilasto';
import Ohje from './components/Ohje';
import HomeIcon from '@mui/icons-material/Home';
import InfoIcon from '@mui/icons-material/Info';
import AnalyticsIcon from '@mui/icons-material/Analytics';
import AddIcon from '@mui/icons-material/Add';
import { Link, Route, Routes } from 'react-router-dom';

function App() {

  const kaynnistetty : React.MutableRefObject<boolean> = useRef(false);

  //Seuraa muistilistalle listtyj tehtvi
  const [tehtavat, setTehtavat] = useState<Tehtava[]>([]);

  //Tilastoi kaikki suoritetut tehtvt
  const [tilastot, setTilastot] = useState<Tilasto[]>([]);

  //Sovelluksen kynnistyess haetaan localStorageen tallennetut tehtvt ja tilastot
  useEffect( () => {

    if (!kaynnistetty.current) {
       if (localStorage.getItem("lista") || localStorage.getItem("tilasto")) {
        setTehtavat(JSON.parse(String(localStorage.getItem("lista"))));
        setTilastot(JSON.parse(String(localStorage.getItem("tilasto"))));
      };
    };

    return () => {
      kaynnistetty.current = true;
    }

  }, [])

  //Tallentaa localStorageen tehtvt ja tilastot aina, kun tehdn muutoksia
  useEffect( () : void => {
    localStorage.setItem("lista", JSON.stringify(tehtavat));
    localStorage.setItem("tilasto", JSON.stringify(tilastot));
  }, [tehtavat, tilastot]);

  //Bottom navin toiminta//
  const [value, setValue] = useState("koti");

  const handleChange = (e: React.SyntheticEvent, newValue: string) => {
    setValue(newValue);
  }
  ////

  return (
    <Container maxWidth="xs" style={{paddingBottom:"5em"}}>

      <Typography 
        variant='h4' 
        textAlign={"center"}
        style={{margin:"20px auto"}}
      >
        Kodinhoidon muistilista
      </Typography>

      <Routes>
        <Route
          path='/'
          element={<Lista 
                      tehtavat={tehtavat} 
                      setTehtavat={setTehtavat} 
                      tilastot={tilastot} 
                      setTilastot={setTilastot}/>}
        />
        <Route
          path='/kirjaus'
          element={<Kirjaus
                      tehtavat={tehtavat} 
                      setTehtavat={setTehtavat} 
                      tilastot={tilastot} 
                      setTilastot={setTilastot}
                      setValue={setValue}/>}
        />
        <Route
          path='/tilastot' 
          element={<Tilasto 
                      tilastot={tilastot}/>}
        />
        <Route
          path='/ohje' 
          element={<Ohje/>}
        />
      </Routes>

      <BottomNavigation 
            style={{
              position:"fixed", 
              bottom:"0", left:"0", right:"0",
              backgroundColor:"#FFEAA9",
            }}
            value={value} 
            onChange={handleChange}
            showLabels
      >
        <BottomNavigationAction component={Link} to="/" label="Koti" value="koti" icon={<HomeIcon/>}/>
        <BottomNavigationAction component={Link} to="/kirjaus" label="Lis" value="lisaa" icon={<AddIcon/>}/>
        <BottomNavigationAction component={Link} to="/tilastot" label="Tilastot" value="tilastot" icon={<AnalyticsIcon/>}/>
        <BottomNavigationAction component={Link} to="/ohje" label="Ohje" value="ohje" icon={<InfoIcon/>}/>
      </BottomNavigation>

    </Container>
);
}

export default App;