import { Container, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Typography } from '@mui/material';
import { format } from 'date-fns';
import React, {} from 'react';

interface Props {
    tilastot : Tilasto[],
}

const Tilasto : React.FC<Props> = ( {tilastot} ) : React.ReactElement => {

    //Suodatetaan tilastoista yksillliset kuvaustekstit 
    const yksilolliset : Tilasto[] = [];
    
    tilastot.filter( (tehtava) => {
      let i = yksilolliset.findIndex( x => x.kuvaus === tehtava.kuvaus);
      if (i <= -1) {
        yksilolliset.push({kuvaus: tehtava.kuvaus, suoritettu: tehtava.suoritettu})
      }
      return null;
    } );
    
    //Laskee parametrina annetun tehtvn suorituskerrat edellisen vuoden ajalta
    const laske = (arg0 : string) : number=> {
      let summa1v : number = 0;
      for (let i = 0; i < tilastot.length; i++){
        if (tilastot[i].kuvaus === arg0 
          && new Date().getTime() - tilastot[i].suoritettu < 365*24*60*60*1000) {
            summa1v++;
        }
      }
      return summa1v;
    }

    //Etsii parametrina annetun tehtvn viimeisimmn suorituskerran pivmrn
    const etsiViimeinen = (arg0 : string) : number => {
      let viimeinen : number = 0;
      for (let i = 0; i < tilastot.length; i++){
        if (tilastot[i].kuvaus === arg0){
          if ( tilastot[i].suoritettu > viimeinen ) {
            viimeinen = tilastot[i].suoritettu;
          }
        }
      }
      return viimeinen;
    }

    return (
        <Container style={{paddingBottom:"5em"}}>
        {(yksilolliset.length === 0)
            ? <Typography textAlign={"center"} style={{marginTop:"45%"}}>Ei tilastoituja kirjauksia</Typography>
            
            : <TableContainer>

              <Table size='small'>
                <TableHead>
                  <TableRow>
                    <TableCell>Tehtv</TableCell>
                    <TableCell>Edellinen suoritus</TableCell>
                    <TableCell>Kerrat (1v)</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {yksilolliset
                    .sort( (a, b) => a.kuvaus.localeCompare(b.kuvaus) )
                    .map( (tilasto : Tilasto, idx : number) => {

                      return (
                        <TableRow key={idx}>
                              <TableCell>{tilasto.kuvaus.toUpperCase()}</TableCell> 
                              <TableCell>{format(etsiViimeinen(tilasto.kuvaus), "dd.MM.yyyy HH:mm")}</TableCell>
                              <TableCell>{laske(tilasto.kuvaus)}</TableCell>
                        </TableRow>
                      )
                    })
                  }
                </TableBody>

            </Table>
            </TableContainer>
          }
        </Container>
        )
}

export default Tilasto;