import { Button, Dialog, DialogActions, DialogContent, DialogContentText, IconButton, List, ListItem, ListItemButton, ListItemIcon, ListItemText, Typography } from '@mui/material';
import React, { useState } from 'react';
import { fi } from 'date-fns/locale';
import { formatDistance } from 'date-fns';
import CloseIcon from '@mui/icons-material/Close';

interface Props {
    tehtavat : Tehtava[]
    tilastot : Tilasto[]
    setTehtavat : (arg0 : Tehtava[]) => void
    setTilastot : (arg0 : Tilasto[]) => void
}

const Kirjaus : React.FC<Props> = ( {tehtavat, setTehtavat, tilastot, setTilastot} ) : React.ReactElement => {

    //Haetaan nykyhetken kellonaika millisekunteina
    const nyt : number = new Date().getTime();

    //Muuttujaan tallennetaan suoritettavan tai poistettavan tehtvn tiedot
    const [kasiteltava, setKasiteltava] = useState<Tehtava>();

    //Poistaa muistilistalta parametrina annetun tehtvn
    const poista = (arg0 : string) : void => {

      setTehtavat( tehtavat.filter( (tehtava : Tehtava) => {
        return (tehtava.id !== arg0)
      } ) );
      handleClose();
    } 

    //Merkitsee parametrin annetun tehtvn suoritetuksi nykyhetkell
    const suorita = (arg0 : string) : void => {
      let suoritettavaKirjaus : Tehtava = tehtavat.filter( (tehtava : Tehtava) => {
        return (tehtava.id === arg0)
      })[0];
      suoritettavaKirjaus.suoritettu = nyt;
      
      let alkuperaiset : Tehtava[] = tehtavat.filter( (tehtava : Tehtava) => {
        return (tehtava.id !== arg0)
      } );

      let uusiLista = [...alkuperaiset, suoritettavaKirjaus]
      let uusiTilastoLista = [...tilastot, suoritettavaKirjaus]
      setTehtavat(uusiLista);
      setTilastot(uusiTilastoLista);
      handleClose();
    }

    //Dialog-ikkunan toiminnallisuudet//
    const [openPoista, setOpenPoista] = useState<boolean>(false);
    const [openSuorita, setOpenSuorita] = useState<boolean>(false);

    const handleClose = () : void => {
      setOpenPoista(false)
      setOpenSuorita(false)
    }
    ////

    return (
        <>
        {(tehtavat.length === 0)
            ? <Typography textAlign={"center"} style={{marginTop:"45%"}}>Ei tehtvi tallennettuna</Typography>
            
            : <List > 
            {tehtavat
              .sort( (a, b) => ((a.suoritettu + a.teeAjassa ) - (b.suoritettu + b.teeAjassa)) )
              .map( (tehtava : Tehtava, idx : number) => {
    
                  return (
                      <ListItem 
                      key={tehtava.id} 
                      style={{
                          display:"flex", 
                          backgroundColor: 
                              ((tehtava.suoritettu + tehtava.teeAjassa) < nyt) 
                              ? "#FA7E4D"
                              : ((tehtava.suoritettu + tehtava.teeAjassa - 2*24*60*60*1000) < nyt)
                                ? "#F3FA61"
                                : "#37ED70",
                          margin:"2px",
                          borderRadius:"5px",
                        }}
                      >
                      <ListItemButton
                        onClick={ () => {setKasiteltava(tehtava); setOpenSuorita(true)} }
                        style={{padding:0}}
                      >
                        <ListItemText 
                          style={{textAlign:"left"}}
                          primary={tehtava.kuvaus.toUpperCase()}
                          secondary={
                            ((tehtava.suoritettu + tehtava.teeAjassa) > nyt)
                            ? formatDistance(nyt, (tehtava.suoritettu + tehtava.teeAjassa), {locale:fi})
                            : "Heti"
                          }
                        />
                      </ListItemButton>
                      <ListItemIcon style={{justifyContent:"flex-end"}}>
                        <IconButton style={{padding:10}} onClick={ () => {setKasiteltava(tehtava); setOpenPoista(true)}}>
                          <CloseIcon />
                        </IconButton>
                      </ListItemIcon>
                      </ListItem>
                  )
              }
              )
            }

          <Dialog
            id="delete-task-dialog"
            open={openPoista}
            onClose={handleClose}
          >
            <DialogContent>
              <DialogContentText>
                {`Haluatko varmasti poistaa suoritteen ${kasiteltava?.kuvaus.toUpperCase()}?`}
              </DialogContentText>
            </DialogContent>
            <DialogActions>
              <Button onClick={ () => {poista(kasiteltava!.id)}}>Poista</Button>
              <Button onClick={handleClose}>Peruuta</Button>
            </DialogActions>
          </Dialog>

          <Dialog
            id="execute-task-dialog"
            open={openSuorita}
            onClose={handleClose}
          >
            <DialogContent>
              <DialogContentText>
                {`Haluatko varmasti suorittaa tehtvn ${kasiteltava?.kuvaus.toUpperCase()}?`}
              </DialogContentText>
            </DialogContent>
            <DialogActions>
              <Button onClick={ () => {suorita(kasiteltava!.id)}}>Suorita</Button>
              <Button onClick={handleClose}>Peruuta</Button>
            </DialogActions>
          </Dialog>

            </List>
          }
        </>
        )
    
}

export default Kirjaus;